.ms-alert .ms-close {
  /* General */
  width: 20px;
  height: 20px;
  display: block;
  /* Position */
  position: absolute;
  top: 2px;
  right: 2px;
  /* Text */
  text-align: center;
  /* Cursor */
  cursor: pointer;
}
.ms-alert .ms-close:before, .ms-alert .ms-close:after {
  /* General */
  height: 10px;
  width: 2px;
  /* Position */
  position: absolute;
  left: 9px;
  top: 5px;
  /* Content */
  content: " ";
  /* Color */
  background-color: rgba(var(--main-text-color), 1);
}
