.ms-label .ms-close {
  /* General */
  height: 15px;
  width: 15px;
  position: relative;
  display: inline-block;
  overflow: hidden;
  margin-left: 2px;
  /* Text */
  text-align: center;
  /* Cursor */
  cursor: pointer;
}
.ms-label .ms-close:before, .ms-label .ms-close:after {
  /* General */
  height: 10px;
  width: 2px;
  /* Position */
  position: absolute;
  left: 6.7px;
  top: 2.5px;
  /* Content */
  content: " ";
  /* Color */
  background-color: rgba(var(--main-text-color), 1);
}
.ms-label .ms-close:before {
  transform: rotate(45deg);
}
.ms-label .ms-close:after {
  transform: rotate(-45deg);
}
.ms-label .ms-close:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: var(--default-border-radius);
}
.ms-label .ms-close:active {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: var(--default-border-radius);
}

