}
.ms-progress.ms-medium .ms-progress-fill {
  height: 1.7rem;
  line-height: 1.7rem;
}
.ms-progress.ms-large {
  height: 2rem;
}
.ms-progress.ms-large .ms-progress-fill {
  height: 2rem;
  line-height: 2rem;
}
.ms-progress.ms-rounded {
  border-radius: 50px;
}
.ms-progress.ms-rounded .ms-rounded {
  border-radius: 50px;
}
.ms-progress.ms-light {
  height: 0.3rem;
}

.ms-tab {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 2px solid rgba(var(--default-border-color), 1);
  margin: 0;
  padding: 0;
}
.ms-tab ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.ms-tab ul li {
  display: inline-block;
  list-style: none;
  margin: 0;
  padding: 0;
}
.ms-tab ul li a {
  display: block;
  font-size: 1rem;
  color: rgba(var(--default-text-color), 0.1);
  text-decoration: none;
  margin: 0;
  padding: 1rem;
  border-bottom: 2px solid transparent;
}
.ms-tab ul li a:hover {
  border-bottom: 4px solid rgba(var(--default-border-color), 1);
}
.ms-tab ul li a.ms-active {
  border-bottom: 4px solid rgba(var(--default-border-color), 1);
}
.ms-tab ul li.ms-logo img {
  max-height: 30px;
  vertical-align: bottom;
}
.ms-tab ul li.ms-logo a:hover {
  border-bottom: none;
}
.ms-tab.ms-fullwidth ul {
  display: flex;
  flex-wrap: wrap;
}
.ms-tab.ms-fullwidth ul li {
  overflow: hidden;
  text-align: center;
  flex: 1;
}
.ms-tab.ms-fullwidth ul li a {
  width: 100%;
}

.ms-table {
  width: 100%;
  display: table;
  border-spacing: 0;
  margin-bottom: 1rem;
  color: rgba(var(--default-bg-color), 1);
  padding: 0;
}
.ms-table thead {
  color: rgba(var(--default-bg-color), 1);
  text-align: left;
}
.ms-table td,
.ms-table th {
  color: rgba(var(--default-bg-color), 1);
  padding: 0.8rem 0.4rem;
  border-bottom: 2px solid rgba(var(--default-border-color), 1);
}
.ms-table th {
  font-weight: bold;
}
.ms-table.ms-striped td,
.ms-table.ms-striped th {
  border: none;
}
.ms-table.ms-striped tbody tr:nth-of-type(odd) {
  background: rgba(var(--light-bg-color), 1);
}
.ms-table.ms-border {
  border: 2px solid rgba(var(--default-border-color), 1);
}

/* Title */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Inter, sans-serif;
  padding: 0;
}

h1 {
  font-size: 3.5rem;
  margin: 1rem 0;
}

h2 {
  font-size: 3rem;
  margin: 1rem 0;
}

h3 {
  font-size: 2.5rem;
  margin: 1rem 0;
}

h4 {
  font-size: 2rem;
  margin: 1rem 0;
}

h5 {
  font-size: 1.5rem;
  margin: 1rem 0;
}

h6 {
  font-size: 1.25rem;
  margin: 1rem 0;
}

/* Mark */
mark,
.ms-mark {
  /* Color */
  display: inline;
  background-color: #ffe9b3;
  color: rgb(25, 25, 25);
  /* Border */
  border-bottom: 0.05rem solid #ffd367;
  border-radius: var(--default-border-radius);
  /* Margin */
  padding: 0.1rem 0.2rem;
}

/* Code */
code,
.ms-code {
  /* Color */
  display: inline;
  background-color: rgba(224, 69, 64, 0.0823529412);
  color: #e04540;
  /* Text */
  font-family: Courier, monospace;
  /* Border */
  border-radius: var(--default-border-radius);
  /* Margin */
  padding: 0.2rem 0.4rem;
}

/* Terminal */
kbd,
.ms-kbd {
  /* Text */
  display: inline;
  background-color: #000;
  color: #ffffff;
  font-family: Courier, monospace;
  /* Border */
  border-radius: var(--default-border-radius);
  /* Margin */
  padding: 0.2rem 0.2rem;
}

/* Sementic */
del,
s {
  text-decoration: line-through;
}

ins,
u {
  text-decoration: underline;
}

small {
  font-size: 80%;
}

b,
strong {
  font-weight: bold;
}

i,
cite {
  font-style: italic;
}

pre i,
pre cite {
  font-style: normal;
}

sup {
  /* General */
  position: relative;
  top: -0.4rem;
  /* Text */
  line-height: 0;
  vertical-align: baseline;
  font-size: 75%;
}

sub {
  /* General */
  position: relative;
  bottom: -0.2rem;
  /* Test */
  line-height: 0;
  font-size: 75%;
  vertical-align: baseline;
}

/* Blockquote */
blockquote,
.ms-blockquote {
  /* Color */
  color: rgba(var(--main-text-color), 1);
  font-weight: inherit;
  /* Border */
  border-left: 4px solid rgba(var(--default-border-color), 1);
  border-top-right-radius: var(--default-border-radius);
  border-bottom-right-radius: var(--default-border-radius);
  /* Margin */
  margin: 1rem;
  padding: 1rem;
}
blockquote p,
.ms-blockquote p {
  margin: 0 0 0 0;
}
blockquote.ms-center,
.ms-blockquote.ms-center {
  text-align: center;
}
blockquote.ms-primary,
.ms-blockquote.ms-primary {
  border-color: rgba(var(--primary-bg-color), 1);
}
blockquote.ms-secondary,
.ms-blockquote.ms-secondary {
  border-color: rgba(var(--secondary-bg-color), 1);
}
blockquote.ms-action,
.ms-blockquote.ms-action {
  border-color: rgba(var(--action-bg-color), 1);
}
blockquote.ms-action2,
.ms-blockquote.ms-action2 {
  border-color: rgba(var(--action2-bg-color), 1);
}

/* HR */
hr, .ms-hr {
  /* General */
  width: 100%;
  height: 1px;
  /* Color */
  background-color: rgba(var(--light-bg-color), 1);
  /* Border */
  border: none;
  /* Margin */
  margin: 1rem 0;
}

.ms-user {
  /* General */
  width: 100%;
  overflow: auto;
  position: relative;
  box-sizing: border-box;
  /* Colors */
  background-color: transparent;
  color: rgba(var(--main-text-color), 1);
  /* Margins */
  padding: 1rem;
  border-radius: var(--default-border-radius);
}
.ms-user .ms-avatar {
  display: block;
  margin: 0 auto;
}
.ms-user .ms-user-info {
  text-align: center;
  margin: 0 auto;
  margin-top: 1rem;
}
.ms-user .ms-user-info p {
  color: rgba(var(--main-text-color), 1);
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
.ms-user .ms-user-content {
  color: rgba(var(--main-text-color), 1);
  box-sizing: border-box;
  margin: 0 auto;
  margin-top: 1rem;
}
.ms-user .ms-user-pined {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
}
.ms-user.ms-compact .ms-avatar {
  float: left;
  margin: 0;
}
.ms-user.ms-compact .ms-user-info {
  float: left;
  text-align: left;
  margin: 0;
  margin-left: 1rem;
}
.ms-user.ms-compact .ms-user-content {
  float: left;
  width: 100%;
  margin-top: 1rem;
}
.ms-user.ms-borderless {
  border: none;
}
.ms-user.ms-fill {
  background-color: rgba(var(--light-bg-color), 1);
  border: none;
}
.ms-user.ms-border {
  border: 2px solid rgba(var(--default-border-color), 1);
